import React, { Component, Fragment } from 'react';
import { Row, Col, Button, TabContent, TabPane, Nav, NavItem, NavLink } from 'reactstrap';
import DfsTCard from './DfsTCard';
import * as NC from "../../../helper/NetworkingConstants";
import Loader from '../../../components/Loader';
import Pagination from "react-js-pagination";
import LS from 'local-storage';
import { notify } from 'react-notify-toast';
import HF, { _times, _Map, _isEmpty, _isNull, _isUndefined, _remove, _find, _cloneDeep } from "../../../helper/HelperFunction";
import WSManager from '../../../helper/WSManager';
import { DFST_getTournamentFixtures, DFST_getTournamentParticipants, DFST_getTournamentSeasonParticipants, DFST_getTournamentLeaderboard, DFST_getSeasonLeaderboard, DFST_cancelTournament, DFST_deleteTournamentMatch } from '../../../helper/WSCalling';
import { DFST_CANCEL, DELETE_T_MATCH_MSG } from "../../../helper/Message";
import PromptModal from '../../../components/Modals/PromptModal';
import { Base64 } from 'js-base64';
import DfsT_ParticipantsModal from '../../../components/Modals/DfsT_ParticipantsModal';
import DfsT_LeaderBoardModal from '../../../components/Modals/DfsT_LeaderBoardModal';
import DfsTMatchCard from './DfsTMatchCard';
import DfsT_MatchParticipantsModal from '../../../components/Modals/DfsT_MatchParticipantsModal';
import DfsT_MatchLeaderBoardModal from '../../../components/Modals/DfsT_MatchLeaderBoardModal';
import ViewWinners from '../../../components/Modals/ViewWinners';
import CancelReasonModal from '../../../components/Modals/CancelReasonModal';
import queryString from 'query-string';
class DfsDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            SelectedSport: (LS.get('selected_sport')) ? LS.get('selected_sport') : NC.sportsId,
            activeTab: '1',
            UnpubPosting: false,
            sportName: '',
            DeleteModalOpen: false,
            BackTo: (this.props.match.params.pctab) ? this.props.match.params.pctab : '1',
            DfstId: (this.props.match.params.tid) ? this.props.match.params.tid : '0',
            TournamentDtl: [],
            CancelTrnModalOpen: false,
            MerchandiseList: [],
            ApiFlag: '',
            DfsT_usersModalOpen: false,
            DfstT_ParticipantsList: [],
            DfstT_PARTI_CURRENT_PAGE: 1,
            CURRENT_PAGE: 1,
            PERPAGE: NC.ITEMS_PERPAGE,
            TourCompleted: true,
            DfsT_ldrbrdModalOpen: false,
            DfsT_ldrbrdList: [],
            DfsT_LdrBrdPosting: true,
            LDRBRD_CURRENT_PAGE: 1,
            MODAL_PERPAGE: 10,
            DfsT_matchUsersModalOpen: false,
            DfsT_matchLdrbrdModalOpen: false,
            CancelTrnPosting: false,
            CancelPosting: true,
            DeletePosting: false,
        }
    }

    componentDidMount = () => {
        if (HF.allowDFSTournament() != '1')
        {
            notify.show(NC.MODULE_NOT_ENABLE, 'error', 5000)
            this.props.history.push('/dashboard')
        }
        let { SelectedSport } = this.state
        let spNm = HF.getSportsData() ? HF.getSportsData() : []

        if (!_isEmpty(spNm)) {
            var getSportName = spNm.filter(function (item) {
                return item.value === SelectedSport ? true : false;
            });
            let sName = 'cricket'
            if (getSportName)
                sName = getSportName[0].label
            this.setState({ sportName: sName })
        }
        this.getAllFixture()
        this.getMerchandiseList()

    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab,
                CURRENT_PAGE: 1,
                FixtureList: [],
                TotalFixture: 0,
                Posting: true,
            }, function () {
                this.getAllFixture()
            })
        }
    }

    getAllFixture = () => {
        let { activeTab, CURRENT_PAGE, PERPAGE, DfstId } = this.state
        this.setState({ Posting: true })
        let params = {
            match_type: activeTab,
            tournament_id: DfstId,
            items_perpage: PERPAGE,
            current_page: CURRENT_PAGE,
        }

        DFST_getTournamentFixtures(params).then(Response => {
            if (Response.response_code == NC.successCode) {
                let tData = Response.data.tournament
                let TStatus = tData.status ? tData.status : 0;                
                var mEndDate = new Date(WSManager.getUtcToLocal(tData.end_date));
                var curDate = new Date();

                let compDate = false;
                if (curDate >= mEndDate) {
                    compDate = true;
                }
                
                this.setState({
                    FixtureList: Response.data.result ? Response.data.result : [],
                    TotalFixture: Response.data.total ? Response.data.total : 0,
                    TournamentDtl: tData ? tData : [],
                    Posting: false,
                    TourCompleted: (TStatus >= 2 || compDate) ? false : true,
                })
            } else {
                notify.show(NC.SYSTEM_ERROR, 'error', 5000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, 'error', 5000)
        })
    }

    getMerchandiseList = () => {
        let { PERPAGE, CURRENT_PAGE } = this.state
        let params = {
            sort_field: "added_date",
            sort_order: "DESC",
            items_perpage: PERPAGE,
            current_page: CURRENT_PAGE,
        }
        WSManager.Rest(NC.baseURL + NC.GET_MERCHANDISE_LIST, params).then(Response => {
            if (Response.response_code == NC.successCode) {
                this.setState({
                    MerchandiseList: Response.data.merchandise_list
                })
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, 'error', 5000)
        })
    }

    copyMatchUrl = (item) => {
        let { sportName } = this.state
        const el = document.createElement('textarea');
        el.value = NC.baseURL + sportName.toLowerCase() + NC.PickemShareUrl + btoa(item.tournament_id);

        document.body.appendChild(el);
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);
        notify.show("Copied to clipboard", "success", 2000)
    }

    promoteFx = (val) => {   
        // val.season_game_uid = 48470     
        let qStr = HF.promoteFixture(val)
        const stringified = queryString.stringify(qStr);
        this.props.history.push(`/marketing/new_campaign?${stringified}`);
        return false;
    }

    renderCommonView = () => {
        let { activeTab, Posting, CURRENT_PAGE, PERPAGE, TotalFixture, FixtureList } = this.state
        return (
            <Fragment>
                <Row className="mt-30">
                    {
                        (TotalFixture) > 0 ?
                            _Map(FixtureList, (item, idx) => {
                                return (
                                    <DfsTMatchCard
                                        key={idx}
                                        activeTab={activeTab}
                                        item={item}
                                        partiModalCallback={(data) => this.DfsT_matchUsersModal(data)}
                                        ldrbrdModalCallback={(data) => this.DfsT_matchLdrbrdModal(data)}
                                        deleteMatchToggle={(data) => this.deleteMatchToggle(data, idx)}
                                        promote={(data) => this.promoteFx(data)}
                                    />
                                )
                            })
                            :
                            <Col md={12}>
                                {(TotalFixture == 0 && !Posting) ?
                                    <div className="no-records">{NC.NO_RECORDS}</div>
                                    :
                                    <Loader />
                                }
                            </Col>
                    }
                </Row>
                {
                    TotalFixture > PERPAGE && (
                        <Row className="mb-20">
                            <Col md={12}>
                                <div className="custom-pagination float-right">
                                    <Pagination
                                        activePage={CURRENT_PAGE}
                                        itemsCountPerPage={PERPAGE}
                                        totalItemsCount={TotalFixture}
                                        pageRangeDisplayed={5}
                                        onChange={e => this.handlePageChange(e)}
                                    />
                                </div>
                            </Col>
                        </Row>
                    )
                }
            </Fragment>
        )
    }

    handlePageChange(current_page) {
        if (current_page != this.state.CURRENT_PAGE) {
            this.setState({
                CURRENT_PAGE: current_page
            }, this.getAllFixture);
        }
    }

    deleteMatchToggle = (item, idx) => {
        this.setState({
            deleteIndex: idx,
            Delete_Match_ID: item.tournament_season_id,
            DeleteModalOpen: !this.state.DeleteModalOpen,
            CancelReason: '',
            CancelPosting: true
        })
    }

    deleteMatch = () => {
        this.setState({ DeletePosting : true })
        const { deleteIndex, Delete_Match_ID, FixtureList, TournamentDtl } = this.state
        const param = {
            tournament_id: TournamentDtl.tournament_id ? TournamentDtl.tournament_id : '',
            tournament_season_id: Delete_Match_ID,            
        }
        
        let tempFixtureList = FixtureList
        DFST_deleteTournamentMatch(param).then((responseJson) => {
            if (responseJson.response_code === NC.successCode) {
                _remove(tempFixtureList, function (item, idx) {
                    return idx == deleteIndex
                })
                this.deleteMatchToggle({}, deleteIndex)
                notify.show(responseJson.message, "success", 5000);
                this.setState({
                    FixtureList: tempFixtureList,
                    DeletePosting: false
                })
            }
        }).catch((error) => {
            notify.show(NC.SYSTEM_ERROR, "error", 5000);
        })
    }

    backToTornament = () => {
        this.props.history.push('/game_center/DFS?pctab=3&tab=' + this.state.BackTo)
    }

    CancelTournToggle = (t_id) => {
        this.setState({
            CancelTrnModalOpen: !this.state.CancelTrnModalOpen,
            TournamentUId: t_id,
            CancelReason: '',
            CancelPosting: true
        });
    }

    redirectTornamentDtl = () => {
        return null
    }

    redirectTornament = (tournament_id) => {
        this.props.history.push('/game_center/tournament?pid=' + Base64.encode(tournament_id))
    }

    viewWinners = (e, templateObj) => {
        this.setState({
            prize_modal: !this.state.prize_modal,
            templateObj: templateObj
        });
    }

    DfsT_usersModal = (item) => {
        let m_val = !this.state.DfsT_usersModalOpen
        this.setUserModalValues(item, item.tournament_id, 'DfsT_usersModalOpen', m_val)
    }

    DfsT_matchUsersModal = (item) => {
        let m_val = !this.state.DfsT_matchUsersModalOpen
        this.setUserModalValues(item, item.tournament_season_id, 'DfsT_matchUsersModalOpen', m_val)
    }

    setUserModalValues = (item, id, modal_key, modal_value) => {
        this.setState({
            DFST_ID: id,
            DfsT_Users_ITEM: item,
            DfstT_PARTI_CURRENT_PAGE: 1,
            [modal_key]: modal_value
        }, () => {
            if (this.state.DfsT_usersModalOpen || this.state.DfsT_matchUsersModalOpen) {
                this.DFST_getParticipantList(modal_key)
            }
            else {
                this.setState({
                    DfstT_ParticipantsList: [],
                    DfsT_TotalParticipants: 0,
                })
            }
        })
    }

    DFST_getParticipantList = (modal_key) => {
        this.setState({ DfsT_PartiListPosting: true })
        let { DfstT_PARTI_CURRENT_PAGE, MODAL_PERPAGE, DFST_ID } = this.state
        let params = {
            items_perpage: MODAL_PERPAGE,
            current_page: DfstT_PARTI_CURRENT_PAGE,
        }

        let ApiCall = null
        if (modal_key === 'DfsT_usersModalOpen') {
            ApiCall = DFST_getTournamentParticipants
            params.tournament_id = DFST_ID
        }
        if (modal_key === 'DfsT_matchUsersModalOpen') {
            ApiCall = DFST_getTournamentSeasonParticipants
            params.tournament_season_id = DFST_ID
        }

        ApiCall(params).then(Response => {
            if (Response.response_code == NC.successCode) {
                let response = Response.data
                let res = ''
                if (modal_key === 'DfsT_usersModalOpen') {
                    res = !_isUndefined(response.tournament_participants) ? response.tournament_participants : []
                }
                if (modal_key === 'DfsT_matchUsersModalOpen') {
                    res = !_isUndefined(response.list) ? response.list : []
                }

                this.setState({
                    DfstT_ParticipantsList: res,
                    DfsT_TotalParticipants: !_isUndefined(response.total) ? response.total : 0,
                    DfsT_PartiListPosting: false
                })
            } else {
                notify.show(NC.SYSTEM_ERROR, 'error', 5000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, 'error', 5000)
        })
    }

    DfsT_handleUsersPageChange = (current_page, modal_key) => {

        if (this.state.DfstT_PARTI_CURRENT_PAGE !== current_page) {
            this.setState({
                DfstT_PARTI_CURRENT_PAGE: current_page
            }, () => {
                this.DFST_getParticipantList(modal_key)
            });
        }
    }

    DfsT_ldrbrdModal = (item) => {
        let m_val = !this.state.DfsT_ldrbrdModalOpen
        this.setLdrBrdModalValues(item, item.tournament_id, 'DfsT_ldrbrdModalOpen', m_val)
    }

    DfsT_matchLdrbrdModal = (item) => {
        let m_val = !this.state.DfsT_matchLdrbrdModalOpen
        this.setLdrBrdModalValues(item, item.tournament_season_id, 'DfsT_matchLdrbrdModalOpen', m_val)
    }

    setLdrBrdModalValues = (item, id, modal_key, modal_value) => {
        this.setState({
            DFST_ID: id ? id : '',
            DfsT_Users_ITEM: item,
            LDRBRD_CURRENT_PAGE: 1,
            [modal_key]: modal_value
        }, () => {
            if (this.state.DfsT_ldrbrdModalOpen || this.state.DfsT_matchLdrbrdModalOpen) {
                this.getLeaderboardList(modal_key)
            }
            else {
                this.setState({
                    DfsT_ldrbrdList: [],
                    DfsT_TotalLdrbrd: 0,
                })
            }
        })
    }

    getLeaderboardList = (modal_key) => {
        this.setState({ DfsT_LdrBrdPosting: true })
        let { LDRBRD_CURRENT_PAGE, MODAL_PERPAGE, DFST_ID } = this.state
        let params = {
            items_perpage: MODAL_PERPAGE,
            current_page: LDRBRD_CURRENT_PAGE,
        }        

        let ApiCall = null
        if (modal_key === 'DfsT_ldrbrdModalOpen') {
            ApiCall = DFST_getTournamentLeaderboard
            params.tournament_id = DFST_ID
        }
        if (modal_key === 'DfsT_matchLdrbrdModalOpen') {
            ApiCall = DFST_getSeasonLeaderboard
            params.tournament_season_id = DFST_ID
        }

        ApiCall(params).then(Response => {
            if (Response.response_code == NC.successCode) {
                this.setState({
                    DfsT_ldrbrdList: Response.data.leaderboard_data,
                    DfsT_TotalLdrbrd: Response.data.total,
                    DfsT_LdrBrdPosting: false
                })
            } else {
                notify.show(NC.SYSTEM_ERROR, 'error', 5000)
            }
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, 'error', 5000)
        })
    }

    handleLdrBrdPageChange = (current_page, modal_key) => {
        if (this.state.LDRBRD_CURRENT_PAGE !== current_page) {
            this.setState({
                LDRBRD_CURRENT_PAGE: current_page
            }, () => {
                this.getLeaderboardList(modal_key)
            });
        }
    }    

    cancelTournament = () => {
        this.setState({ CancelTrnPosting: true })
        let params = {
            tournament_unique_id: this.state.TournamentUId,
            cancel_reason: this.state.CancelReason,
        }
        
        DFST_cancelTournament(params).then(Response => {
            if (Response.response_code == NC.successCode) {
                this.backToTornament()
                notify.show(Response.message, "success", 5000)
            } else {
                notify.show(NC.SYSTEM_ERROR, 'error', 5000)
            }
            this.setState({ CancelTrnPosting: false })
        }).catch(error => {
            notify.show(NC.SYSTEM_ERROR, 'error', 5000)
        })
    }

    cancelInputChange = (e) => {
        let name = e.target.name
        let value = e.target.value
        let btnAction = false
        if (value.length < 3 || value.length > 160)
            btnAction = true

        console.log("name===>", name);

        this.setState({
            [name]: value,
            CancelPosting: btnAction
        },()=>{
            
                console.log("450===>", this.state.CancelReason);
            
        })
    }

    render() {
        let { activeTab, DeleteModalOpen, DeletePosting, TournamentDtl, CancelTrnModalOpen, prize_modal, DfsT_usersModalOpen, DfsT_Users_ITEM, DfstT_ParticipantsList, DfsT_PartiListPosting, DfstT_PARTI_CURRENT_PAGE, DfsT_TotalParticipants, TourCompleted, DfsT_ldrbrdModalOpen, DfsT_ldrbrdList, DfsT_LdrBrdPosting, LDRBRD_CURRENT_PAGE, DfsT_TotalLdrbrd, DfsT_matchUsersModalOpen, MODAL_PERPAGE, DfsT_matchLdrbrdModalOpen, templateObj, MerchandiseList, CancelPosting } = this.state

        let DeleteModalProps = {
            publishModalOpen: DeleteModalOpen,
            publishPosting: DeletePosting,
            modalActionNo: this.deleteMatchToggle,
            modalActionYes: this.deleteMatch,
            MainMessage: DELETE_T_MATCH_MSG,
            SubMessage: '',
        }
        
        let ConfirmModalProps = {
            ModalOpen: CancelTrnModalOpen,
            CancelPosting: CancelPosting,
            modalActionNo: this.CancelTournToggle,
            modalActionYes: this.cancelTournament,
            MainMessage: DFST_CANCEL,
            SubMessage: '',
            cancelInputChange: this.cancelInputChange,
        }

        let DfsT_usersProps = {
            usersModalOpen: DfsT_usersModalOpen,
            closeUserListModal: this.DfsT_usersModal,
            ListItem: DfsT_Users_ITEM,
            ParticipantsList: DfstT_ParticipantsList,
            PartiListPosting: DfsT_PartiListPosting,
            PARTI_CURRENT_PAGE: DfstT_PARTI_CURRENT_PAGE,
            PERPAGE: MODAL_PERPAGE,
            TotalParticipants: DfsT_TotalParticipants,
            handleUsersPageChange: (e) => this.DfsT_handleUsersPageChange(e, 'DfsT_usersModalOpen'),
            activeTab: activeTab
        }

        let DfsT_ldrbrdProps = {
            usersModalOpen: DfsT_ldrbrdModalOpen,
            closeUserListModal: this.DfsT_ldrbrdModal,
            ListItem: DfsT_Users_ITEM,
            ParticipantsList: DfsT_ldrbrdList,
            PartiListPosting: DfsT_LdrBrdPosting,
            PARTI_CURRENT_PAGE: LDRBRD_CURRENT_PAGE,
            PERPAGE: MODAL_PERPAGE,
            TotalParticipants: DfsT_TotalLdrbrd,
            handleUsersPageChange: (e) => this.handleLdrBrdPageChange(e, 'DfsT_ldrbrdModalOpen'),
            activeTab: activeTab,
        }

        let DfsT_match_usersProps = {
            usersModalOpen: DfsT_matchUsersModalOpen,
            closeUserListModal: this.DfsT_matchUsersModal,
            ListItem: DfsT_Users_ITEM,
            ParticipantsList: DfstT_ParticipantsList,
            PartiListPosting: DfsT_PartiListPosting,
            PARTI_CURRENT_PAGE: DfstT_PARTI_CURRENT_PAGE,
            PERPAGE: MODAL_PERPAGE,
            TotalParticipants: DfsT_TotalParticipants,
            handleUsersPageChange: (e) => this.DfsT_handleUsersPageChange(e, 'DfsT_matchUsersModalOpen'),
            activeTab: activeTab
        }

        let DfsT_match_ldrbrdProps = {
            usersModalOpen: DfsT_matchLdrbrdModalOpen,
            closeUserListModal: this.DfsT_matchLdrbrdModal,
            ListItem: DfsT_Users_ITEM,
            ParticipantsList: DfsT_ldrbrdList,
            PartiListPosting: DfsT_LdrBrdPosting,
            PARTI_CURRENT_PAGE: LDRBRD_CURRENT_PAGE,
            PERPAGE: MODAL_PERPAGE,
            TotalParticipants: DfsT_TotalLdrbrd,
            handleUsersPageChange: (e) => this.handleLdrBrdPageChange(e, 'DfsT_matchLdrbrdModalOpen'),
            activeTab: activeTab,
        }

        let DfsT_ViewWinnersProps = {
            modalisOpen: prize_modal,
            templateObj: templateObj,
            merchandise_list: MerchandiseList,
            PrizeModelCallback: this.viewWinners,
        }

        return (
            <div className="dfst-detail view-picks">
                {DeleteModalOpen && <PromptModal {...DeleteModalProps} />}
                {CancelTrnModalOpen && <CancelReasonModal {...ConfirmModalProps} />}
                {prize_modal && <ViewWinners {...DfsT_ViewWinnersProps} />}
                {DfsT_usersModalOpen && <DfsT_ParticipantsModal {...DfsT_usersProps} />}
                {DfsT_ldrbrdModalOpen && <DfsT_LeaderBoardModal {...DfsT_ldrbrdProps} />}
                {DfsT_matchUsersModalOpen && <DfsT_MatchParticipantsModal {...DfsT_match_usersProps} />}
                {DfsT_matchLdrbrdModalOpen && <DfsT_MatchLeaderBoardModal {...DfsT_match_ldrbrdProps} />}
                
                <Row>
                    <Col md={12}>
                        <div className="float-left">
                            <DfsTCard
                                edit={TourCompleted ? true : false}
                                listItem={TournamentDtl}
                                redirectCallback={() => this.redirectTornamentDtl()}
                                getPrizeCallback={(data) => HF.getPrizeAmount(data)}
                                editCallback={(tournament_id) => this.redirectTornament(tournament_id)}
                                viewWinnersCallback={(e, itemObj) => this.viewWinners(e, itemObj)}
                                partiModalCallback={(data) => this.DfsT_usersModal(data, 'tournament')}
                                ldrbrdModalCallback={(data) => this.DfsT_ldrbrdModal(data, 'tournament')}
                            />
                        </div>
                        {
                            TourCompleted &&
                            <div className="float-right">
                                <Button
                                    className="confirm-btn btn-secondary-outline mr-4"
                                    onClick={() => this.CancelTournToggle(TournamentDtl.tournament_unique_id ? TournamentDtl.tournament_unique_id : '')}
                                >Cancel Tournament</Button>
                            </div>
                        }
                    </Col>
                </Row>

                <div className="user-navigation">
                    <Col md={12}>
                        <div className="w-100">
                            <Row>
                                <Col md={6}>
                                    <Nav tabs>
                                        <NavItem className={activeTab === '1' ? "active" : ""}
                                            onClick={() => { this.toggle('1'); }}>
                                            <NavLink>
                                                Live
                                            </NavLink>
                                        </NavItem>
                                        {

                                            <NavItem className={activeTab === '2' ? "active" : ""}
                                                onClick={() => { this.toggle('2'); }}>
                                                <NavLink>
                                                    Upcoming
                                                </NavLink>
                                            </NavItem>
                                        }
                                        {

                                            <NavItem className={activeTab === '3' ? "active" : ""}
                                                onClick={() => { this.toggle('3'); }}>
                                                <NavLink>
                                                    Completed
                                                </NavLink>
                                            </NavItem>
                                        }
                                    </Nav>
                                </Col>
                                <Col md={6}>
                                    <div className="refresh-page" onClick={() => this.backToTornament()}>
                                        <span>{'<'} Back To Tournament</span>
                                    </div>
                                </Col>
                            </Row>

                            <TabContent activeTab={activeTab}>
                                <TabPane tabId="1">
                                    {this.renderCommonView()}
                                </TabPane>
                                <TabPane tabId="2">
                                    <Row>
                                        <Col md={12}>
                                            {this.renderCommonView()}
                                        </Col>
                                    </Row>
                                </TabPane>
                                <TabPane tabId="3">
                                    <Row>
                                        <Col md={12}>
                                            {this.renderCommonView()}
                                        </Col>
                                    </Row>
                                </TabPane>
                            </TabContent>
                        </div>
                    </Col>
                </div>
            </div>
        )
    }
}
export default DfsDetails
